




















































































































































































































































import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import OrganizationInfo from "@/microApps/geographic/components/OrganizationInfo.vue";
import GeographicMap from "@/microApps/geographic/components/GeographicMap.vue";
import Url from "../api";
import Utils from "../../../utils/utils";

@Component({
  components: { GeographicMap, OrganizationInfo }
})
export default class ElectricFence extends Vue {
  // 复选框选中行数据
  multipleSelection: Array<any> = [];
  // 分页信息
  pagination: any = {
    // 查询关键字
    condition: "",
    // 用于查询和显示某页数据
    currentPage: 1,
    // 总条数
    total: 0,
    // 表格信息
    tableData: [],
    // 分页条数
    pageSize: 10
  };
  //围栏ID
  fenceId: any = "";
  //人员分页信息
  personPagination: any = {
    current: 1,
    size: 15,
    total: 0,
    query: {
      name: "",
      typeId: "",
      organizationId: "",
      mobile: "",
      tel: ""
    }
  };
  //车辆分页信息
  vehiclePagination: any = {
    // 查询关键字
    query: {},
    // 表格信息
    tableData: [],
    // 用于查询和显示某页数据
    current: 1,
    // 分页条数
    page: 15,
    // 总条数
    total: 0
  };
  // 按钮名称
  buttonName: string = "绘制电子围栏";
  // 选中的组织信息
  organizationInfo: any = null;
  // 围栏表单
  fenceForm: any = {
    id: "",
    code: "",
    name: "",
    regionCode: [],
    area: "",
    coordinates: "",
    remark: "",
    organizationId: "",
    organizationName: ""
  };
  // 围栏表单规则
  gridFormRules: any = {
    code: [{ required: true, message: "请输入网格编码", trigger: "change" }],
    name: [{ required: true, message: "请输入网格名称", trigger: "change" }],
    regionCode: [{ required: true, message: "请选择区域", trigger: "change" }]
  };
  // 行政区域下拉框
  regionOptions: Array<any> = [];
  // 区域props
  regionProps: any = {
    value: "code",
    label: "name"
  };
  // 弹窗输入框是否可输入，用于查看情况
  disabled: boolean = false;
  // 弹窗显示或者隐藏
  dialogFormVisible: boolean = false;
  // 弹窗名称
  dialogFormVisibleName: string = "";
  // 围栏列表详情弹窗
  detailsVisible: boolean = false;
  // 围栏列表详情名称
  detailsName: string = "";
  //围栏表格展示切换
  fenceVisible: boolean = true;
  //人员绑定显示
  personBindVisible: boolean = false;
  //人员表格信息
  personTableData: any = [];
  //车辆绑定显示
  vehicleBindVisible: boolean = false;
  created() {
    this.getRegions();
    this.getPersonList();
    this.getVehicleList();
  }
  /**
   * 通过组织ID获取围栏列表
   * @param organization 当前组织信息
   */
  getGridListByOrganization(organization: any) {
    this.organizationInfo = organization;
    this.$http
      .post(Url.fence.getElectricFenceByOrganizationId, {
        query: {
          name: this.pagination.condition.split(" ").join(""),
          organizationId: organization.id
        },
        size: this.pagination.pageSize,
        current: this.pagination.currentPage
      })
      .then((response: any) => {
        if (response.data.status) {
          this.pagination.tableData = response.data.data.records;
          this.pagination.total = response.data.data.total;
          this.pagination.currentPage = response.data.data.current;
        } else {
          this.$message.error(response.data.message);
        }
      });
    this.$http
      .post(Url.fence.getElectricFenceByOrganizationId, {
        query: {
          name: this.pagination.condition.split(" ").join(""),
          organizationId: organization.id
        },
        size: 9999,
        current: 1
      })
      .then((response: any) => {
        if (response.data.status) {
          (this.$refs["geographic-map"] as any).polygonShow(response.data.data.records);
        } else {
          this.$message.error(response.data.message);
        }
      });
  }
  // 获取行政区域
  getRegions() {
    this.$http.get(Url.organization.getRegions).then((response: any) => {
      if (response.data.status) {
        this.regionOptions = response.data.data;
        Utils.removeEmptyChildren(this.regionOptions);
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  // 保存围栏
  gridSave(dialogFormVisibleName: string) {
    let addOrUpdateUrl = dialogFormVisibleName === "添加" ? Url.fence.fenceAdd : Url.fence.fenceUpdate;
    let gridFormCopy = JSON.parse(JSON.stringify(this.fenceForm));
    gridFormCopy.regionCode = gridFormCopy.regionCode.toString();
    gridFormCopy.region = (this.$refs["cascadeRegion"] as any).inputValue;
    (this.$refs["fenceForm"] as any).validate((valid: any) => {
      if (valid) {
        this.$http
          .post(addOrUpdateUrl, gridFormCopy)
          .then((response: any) => {
            if (response.data.status) {
              this.$message.success(response.data.message);
              this.getGridListByOrganization(this.organizationInfo);
            } else {
              this.$message.error(response.data.message);
            }
            this.dialogFormVisible = false;
          })
          .catch((response: any) => {
            console.log(response);
            this.$message.error(response.data.message);
            this.dialogFormVisible = false;
          });
      } else {
        console.log("信息有误");
        return false;
      }
    });
  }
  /**
   * 打开网格弹窗
   * @param row  编辑或查看时的数据
   * @param sign 标志（添加、编辑或者删除，可用于弹窗标题显示）
   */
  openForm(row: any = {}, sign: string) {
    if (sign === "添加") {
      this.fenceForm.id = "";
      this.fenceForm.code = "";
      this.fenceForm.name = "";
      this.fenceForm.regionCode = [];
      this.fenceForm.area = row.gridArea;
      this.fenceForm.coordinates = row.coordinates;
      this.fenceForm.remark = "";
    } else {
      this.fenceForm.id = row.id;
      this.fenceForm.code = row.code;
      this.fenceForm.name = row.name;
      this.fenceForm.regionCode = row.regionCode.split(",");
      this.fenceForm.area = row.area;
      this.fenceForm.coordinates = row.coordinates;
      this.fenceForm.remark = row.remark;
    }
    this.disabled = sign === "查看";
    this.fenceForm.organizationId = this.organizationInfo.id;
    this.fenceForm.organizationName = this.organizationInfo.name;
    this.dialogFormVisible = true;
    this.dialogFormVisibleName = sign;
  }
  // 打开网格列表详情弹窗
  detailsView(data: any) {
    this.detailsName = data.name;
    this.$nextTick(() => {
      this.detailsVisible = true;
      this.fenceVisible = true;
      this.personBindVisible = false;
      this.vehicleBindVisible = false;
    });
  }
  // 点击弹窗外或者右上角×触发
  handleCancel() {
    this.dialogFormVisible = false;
  }
  // 展示人员绑定列表
  openPersonList(row: any) {
    this.fenceVisible = false;
    this.personBindVisible = true;
    this.fenceId = row.id;
  }
  // 载入人员列表
  getPersonList() {
    this.$http.post(Url.person.getPaging, this.personPagination).then((res: any) => {
      if (res.status) {
        if (res.data.status) {
          this.personTableData = res.data.data.records;
          this.personPagination.total = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      }
    });
  }
  // 获得所有复选框选中数据
  getAllSelected(val: Array<any>) {
    this.multipleSelection = val;
  }

  // 人员绑定
  bindingPerson(ids: any) {
    this.$http.post(`${Url.fence.bindPerson}${this.fenceId}`, ids).then((res: any) => {
      if (res.data.status) {
        console.log("1123");
        this.$message({
          type: "success",
          message: "绑定成功!"
        });
      } else {
        this.$message({
          showClose: true,
          message: res.data.message,
          type: "error"
        });
      }
    });
  }
  // 批量绑定人物
  multiplePersonBind() {
    let ids: any = [];
    this.multipleSelection.forEach(item => {
      ids.push(item.id);
    });
    this.bindingPerson(ids);
  }
  // 返回围栏页面
  backToFence() {
    this.fenceVisible = true;
    this.vehicleBindVisible = false;
    this.personBindVisible = false;
  }
  // 展示车辆绑定列表
  openVehicleList(row: any) {
    this.fenceVisible = false;
    this.vehicleBindVisible = true;
    this.fenceId = row.id;
  }
  // 根据关键字，每页查询数量，当前页获得所有链表
  getVehicleList() {
    this.$http.post(Url.car.getPaging, this.vehiclePagination).then((response: any) => {
      if (response.data.status) {
        this.vehiclePagination.tableData = response.data.data.records;
        this.vehiclePagination.total = response.data.data.total;
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  //批量绑定车辆
  multipleVeguckeBind() {
    let ids: any = [];
    this.multipleSelection.forEach(item => {
      ids.push(item.id);
    });
    this.bindingVehicle(ids);
  }
  bindingVehicle(ids: any) {
    this.$http.post(`${Url.fence.bindCar}${this.fenceId}`, ids).then((res: any) => {
      if (res.data.status) {
        this.$message({
          type: "success",
          message: "绑定成功!"
        });
      } else {
        this.$message({
          showClose: true,
          message: res.data.message,
          type: "error"
        });
      }
    });
  }
}
